@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
*{
  margin: 0;
}
body {
  background-image: url(/static/media/back.d87d71e9.jpg);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.post{
    display: flex;
    flex-direction: column;
    flex:0.65 1;
    margin-top:50px;
}
.card1{
    border-radius: 5%;
    background-color: #e6ffee;
}
.top-image{
    display: flex;
}
.top-image .dot{
    margin-left: 140px;
    font-size: 20px;
}
.top-image .dot:hover{
    cursor: pointer;
}
.icon:hover{
    cursor: pointer;
}
.card2{
    margin-top: 50px;
    width: 300px;
    padding: 10px;
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family:'loto' sans-serif;
}
.head{
    /* position: fixed; */
    width: 80%;
    background-color:#fff;
    margin-top: 10px;
    box-shadow: 0 5px 5px rgb(202, 176, 128);
    display: flex;
    justify-content: center;
}
.title{
    font-size: bold;
    padding: 10px;
}
.img{
    height: 70px;
    width: 70px;
    border-radius: 50px;
}
.main{
    display: flex;
    justify-content: center;
}
